@import '../../style/themes/index';
@import '../../style/mixins/index';

@tooltip-prefix-cls: ~'@{ant-prefix}-tooltip';

@tooltip-arrow-shadow-width: 3px;

@tooltip-arrow-rotate-width: sqrt(@tooltip-arrow-width * @tooltip-arrow-width * 2) +
  @tooltip-arrow-shadow-width * 2;

@tooltip-arrow-offset-vertical: 5px; // 8 - 3px
@tooltip-arrow-offset-horizontal: 13px; // 16 - 3px

// Base class
.@{tooltip-prefix-cls} {
  .reset-component();

  position: absolute;
  z-index: @zindex-tooltip;
  display: block;
  max-width: @tooltip-max-width;
  visibility: visible;

  &-hidden {
    display: none;
  }

  &-placement-top,
  &-placement-topLeft,
  &-placement-topRight {
    padding-bottom: @tooltip-distance;
  }

  &-placement-right,
  &-placement-rightTop,
  &-placement-rightBottom {
    padding-left: @tooltip-distance;
  }

  &-placement-bottom,
  &-placement-bottomLeft,
  &-placement-bottomRight {
    padding-top: @tooltip-distance;
  }

  &-placement-left,
  &-placement-leftTop,
  &-placement-leftBottom {
    padding-right: @tooltip-distance;
  }

  // Wrapper for the tooltip content
  &-inner {
    min-width: 30px;
    min-height: 32px;
    padding: 6px 8px;
    color: @tooltip-color;
    text-align: left;
    text-decoration: none;
    word-wrap: break-word;
    background-color: @tooltip-bg;
    border-radius: @border-radius-base;
    box-shadow: @box-shadow-base;
  }

  // Arrows
  &-arrow {
    position: absolute;
    display: block;
    width: @tooltip-arrow-rotate-width;
    height: @tooltip-arrow-rotate-width;
    overflow: hidden;
    background: transparent;
    pointer-events: none;

    &-content {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      display: block;
      width: @tooltip-arrow-width;
      height: @tooltip-arrow-width;
      margin: auto;
      background-color: @tooltip-bg;
      content: '';
      pointer-events: auto;
    }
  }

  &-placement-top &-arrow,
  &-placement-topLeft &-arrow,
  &-placement-topRight &-arrow {
    bottom: @tooltip-distance - @tooltip-arrow-rotate-width;

    &-content {
      box-shadow: @tooltip-arrow-shadow-width @tooltip-arrow-shadow-width 7px fade(@black, 7%);
      transform: translateY((-@tooltip-arrow-rotate-width / 2)) rotate(45deg);
    }
  }

  &-placement-top &-arrow {
    left: 50%;
    transform: translateX(-50%);
  }

  &-placement-topLeft &-arrow {
    left: @tooltip-arrow-offset-horizontal;
  }

  &-placement-topRight &-arrow {
    right: @tooltip-arrow-offset-horizontal;
  }

  &-placement-right &-arrow,
  &-placement-rightTop &-arrow,
  &-placement-rightBottom &-arrow {
    left: @tooltip-distance - @tooltip-arrow-rotate-width;

    &-content {
      box-shadow: -@tooltip-arrow-shadow-width @tooltip-arrow-shadow-width 7px fade(@black, 7%);
      transform: translateX((@tooltip-arrow-rotate-width / 2)) rotate(45deg);
    }
  }

  &-placement-right &-arrow {
    top: 50%;
    transform: translateY(-50%);
  }

  &-placement-rightTop &-arrow {
    top: @tooltip-arrow-offset-vertical;
  }

  &-placement-rightBottom &-arrow {
    bottom: @tooltip-arrow-offset-vertical;
  }

  &-placement-left &-arrow,
  &-placement-leftTop &-arrow,
  &-placement-leftBottom &-arrow {
    right: @tooltip-distance - @tooltip-arrow-rotate-width;

    &-content {
      box-shadow: @tooltip-arrow-shadow-width -@tooltip-arrow-shadow-width 7px fade(@black, 7%);
      transform: translateX((-@tooltip-arrow-rotate-width / 2)) rotate(45deg);
    }
  }

  &-placement-left &-arrow {
    top: 50%;
    transform: translateY(-50%);
  }

  &-placement-leftTop &-arrow {
    top: @tooltip-arrow-offset-vertical;
  }

  &-placement-leftBottom &-arrow {
    bottom: @tooltip-arrow-offset-vertical;
  }

  &-placement-bottom &-arrow,
  &-placement-bottomLeft &-arrow,
  &-placement-bottomRight &-arrow {
    top: @tooltip-distance - @tooltip-arrow-rotate-width;

    &-content {
      box-shadow: -@tooltip-arrow-shadow-width -@tooltip-arrow-shadow-width 7px fade(@black, 7%);
      transform: translateY((@tooltip-arrow-rotate-width / 2)) rotate(45deg);
    }
  }

  &-placement-bottom &-arrow {
    left: 50%;
    transform: translateX(-50%);
  }

  &-placement-bottomLeft &-arrow {
    left: @tooltip-arrow-offset-horizontal;
  }

  &-placement-bottomRight &-arrow {
    right: @tooltip-arrow-offset-horizontal;
  }
}

.generator-tooltip-preset-color(@i: length(@preset-colors)) when (@i > 0) {
  .generator-tooltip-preset-color(@i - 1);
  @color: extract(@preset-colors, @i);
  @lightColor: '@{color}-6';
  .@{tooltip-prefix-cls}-@{color} {
    .@{tooltip-prefix-cls}-inner {
      background-color: @@lightColor;
    }
    .@{tooltip-prefix-cls}-arrow {
      &-content {
        background-color: @@lightColor;
      }
    }
  }
}
.generator-tooltip-preset-color();

@import './rtl';
/**
 * 主题，定制antd主题
 */

// -------- Colors -----------
@primary-color: var(--theme-primary-color);
@info-color: var(--theme-info-color);
@success-color: var(--theme-success-color);
@error-color: var(--theme-error-color);
@warning-color: var(--theme-warning-color);

// Color used by default to control hover and active backgrounds and for
// alert info backgrounds.
@primary-1: #eef0fd; // replace tint(@primary-color, 90%)
@primary-2: #dce1fa; // replace tint(@primary-color, 80%)
@primary-5: #7387eb; // color used to control the text color in many active and hover states, replace tint(@primary-color, 20%)
@primary-7: #5971e7; // replace shade(@primary-color, 5%)

// Base Scaffolding Variables
// ---
// Background color for `<body>`
@body-background: var(--theme-body-bg);
// Base background color for most components
@component-background: var(--theme-component-bg);
@font-family: "Helvetica Neue", "Helvetica", "Arial", "PingFangSC-Regular", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", "sans-serif";
@code-family: Consolas, Menlo, Courier, monospace;
@heading-color: var(--theme-text-color);
@text-color: var(--theme-text-color);
@text-color-secondary: var(--theme-text-color-secondary);
@font-variant-base: normal; // https://github.com/ant-design/ant-design/issues/12690
@font-size-base: 14px;
@font-size-lg: 16px;
@font-size-sm: 12px;
@line-height-base: 1.5715;
@border-radius-base: 4px;
@border-radius-sm: 2px;
@height-base: 32px;
@height-lg: 40px;
@height-sm: 24px;

// The background colors for active and hover states for things like
// list items or table cells.
@item-hover-bg: var(--item-hover-bg);
@item-active-bg: var(--theme-secondary-color-hover);

// LINK
@link-color: var(--link-color);
@link-hover-color: var(--link-hover-color);
@link-active-color: var(--link-active-color);
@link-decoration: none;
@link-hover-decoration: none;

// Border color
// 元素边框
@border-color-base: var(--theme-border-color-base);
@border-color-split: var(--theme-border-color-split); // split border inside a component
@border-color-inverse: var(--theme-border-color-inverse);
@border-width-base: 1px;
@border-style-base: solid;

@background-color-light: var(--theme-bg-color-light); // background of header and selected item
@background-color-base: var(--theme-bg-color-base);

// Disabled states
// ---
@disabled-color: var(--theme-disabled-color);
@disabled-bg: var(--theme-component-bg);

// Input
// ---
@input-height-sm: @height-sm;
@input-height-base: @height-base;
@input-height-lg: @height-lg;
@input-hover-border-color: var(--theme-border-color-base);
@input-placeholder-color: var(--theme-placeholder-color);

// Table
// ---
@table-row-hover-bg: var(--table-row-hover-bg);
@table-header-bg: var(--table-header-bg);
@table-header-color: var(--table-header-color);
@table-padding-vertical: 9.5px; // UE要求行号40，行高= {padding-vertical} * 2 + {font-size} * 1.5

// Menu
// ---
@menu-inline-toplevel-item-height: 32px;
@menu-item-height: 32px;
@menu-item-group-height: 32px;
@menu-collapsed-width: 80px;
@menu-bg: var(--menu-bg);
@menu-popup-bg: var(--menu-popup-bg);
@menu-item-color: var(--menu-item-color);
@menu-highlight-color: var(--menu-highlight-color);
@menu-item-active-bg: var(--menu-item-active-bg);
@menu-item-active-border-width: 3px;
@menu-item-group-title-color: var(--menu-item-group-title-color);
@menu-icon-size: @font-size-base;
@menu-icon-size-lg: @font-size-lg;
@menu-item-vertical-margin: 0;
@menu-item-boundary-margin: 0;

// Collapse
// ---
@collapse-header-padding: 12px 16px;
@collapse-header-padding-extra: 40px;
@collapse-header-bg: var(--collapse-header-bg);
@collapse-content-bg: var(--collapse-content-bg);

// Select
// ---
@select-item-selected-color: @text-color;
@select-item-selected-bg: var(--select-item-selected-bg);
@select-item-active-bg: var(--item-hover-bg); // 这个是item hover的颜色
@select-item-selected-font-weight: normal;
@select-selection-item-bg: var(--color-gray-3);

// Alert
// ---
@alert-success-border-color: var(--alert-success-bg-color);
@alert-success-bg-color: var(--alert-success-bg-color);
@alert-success-icon-color: var(--alert-success-icon-color);
@alert-info-border-color: var(--alert-info-bg-color);
@alert-info-bg-color: var(--alert-info-bg-color);
@alert-info-icon-color: var(--alert-info-icon-color);
@alert-warning-border-color: var(--alert-warning-bg-color);
@alert-warning-bg-color: var(--alert-warning-bg-color);
@alert-warning-icon-color: var(--alert-warning-icon-color);
@alert-error-border-color: var(--alert-error-bg-color);
@alert-error-bg-color: var(--alert-error-bg-color);
@alert-error-icon-color: var(--alert-error-icon-color);
@alert-message-color: var(--theme-text-color);
@alert-text-color: var(--theme-text-color);
@alert-close-color: var(--link-color);
@alert-with-description-padding: var(--alert-with-description-padding);
@alert-with-description-icon-top: var(--alert-with-description-icon-top);

// Tree
@tree-directory-selected-color: var(--tree-directory-selected-color, #fff);
@tree-directory-selected-bg: var(--tree-directory-selected-bg, --theme-primary-color);
@tree-node-hover-bg: var(--tree-node-hover-bg, --theme-secondary-color-hover);
@tree-node-selected-bg: var(--tree-node-selected-bg, #dce1fa); // @primary-2

//Button
// ---
@btn-border-radius-base: var(--btn-border-radius-base);
@btn-default-color: var(--theme-text-color);
@btn-default-bg: var(--color-gray-1);
@btn-default-border: var(--color-gray-5);
@btn-primary-color: var(--color-gray-1);
@btn-primary-bg: var(--color-brand-6);
@btn-shadow: 0 2px 0 rgba(0, 0, 0, 0);
@btn-line-height: 1.5715;
@btn-disable-color: var(--btn-disable-color);
@btn-disable-bg: var(--btn-disable-bg);
@btn-disable-border: var(--btn-disable-border);

// Tooltip
// ---
// Tooltip text color
@tooltip-color: var(--tooltip-color);
// Tooltip background color
@tooltip-bg: var(--tooltip-bg);

// Outline
// ---
@outline-width: 1px;
@outline-blur-size: 0px;
@outline-color: var(--theme-border-focus-color);

// Form
// ---
@label-color: var(--color-gray-8);
@form-item-margin-bottom: 24px;

// Tabs
// ---
@tabs-active-color: var(--link-color);
@tabs-hover-color: var(--link-color);

// Box-shadow
// ---
@shadow-2: var(--shadow-1);

// Avatar
// --
@avatar-size-sm: 16px;
@avatar-size-base: 24px;
@avatar-size-lg: 32px;
@avatar-font-size-sm: 12px;
@avatar-font-size-base: 12px;
@avatar-font-size-lg: 14px;

// Tag
@tag-default-bg: var(--tag-default-bg);
@tag-default-color: @text-color;
@tag-font-size: @font-size-base;
@tag-line-height: 22px;

// Pagination
//--
@pagination-item-bg-active: var(--color-brand-1);

// Badge
// ---
@badge-color: var(--color-error-6);

// Radio
// ---
@radio-focus-shadow: 0 0 0 1px var(--theme-border-focus-color);
@radio-duration: 0;
@radio-button-hover-color: var(--radio-button-hover-color);

// Modal
// ---
@modal-header-bg: var(--modal-header-bg);
@modal-header-close-size: var(--modal-header-close-size);
@modal-header-border-color-split: var(--modal-header-bg);
@modal-body-padding: 24px;
@modal-footer-padding-vertical: 24px;
@modal-footer-padding-horizontal: 24px;
@modal-confirm-body-padding: 40px 30px 15px 30px;

// Progress
// ---
@progress-default-color:var(--theme-primary-color);
@progress-text-color: var(--progress-text-color);
@progress-radius: var(--progress-radius);

// Dropdown
// ---
@dropdown-edge-child-vertical-padding: 8px;

// PageHeader
// ---
@page-header-heading-title: 24px;
